import { Fragment } from 'react';
import MarkdownPreview from '@uiw/react-markdown-preview';
import rehypeRewrite from 'rehype-rewrite';
import { Component } from 'react';
import Code from './Code';
import styles from './index.module.css';
import Footer from '../Footer';

const getCodeStr = (data = [], code = '') => {
  data.forEach((node) => {
    if (node.type === 'text') {
      code += node.value;
    } else if (node.children && Array.isArray(node.children)) {
      code += getCodeStr(node.children);
    }
  });
  return code;
};

export default class Markdown extends Component {
  render() {
    const { editePath } = this.props;
    return (
      <Fragment>
        <MarkdownPreview
          style={{ padding: '20px 26px 40px 26px' }}
          source={this.props.source}
          rehypePlugins={[
            [
              rehypeRewrite,
              {
                rewrite: (node) => {
                  if (
                    node.type === 'element' &&
                    node.tagName === 'pre' &&
                    node.properties['data-type'] === 'rehyp'
                  ) {
                    node.properties['className'].push(styles.rehyp);
                  }
                },
              },
            ],
          ]}
          components={{
            /**
             * bgWhite è®¾ç½®ä»£ç é¢„è§ˆèƒŒæ™¯ç™½è‰²ï¼Œå¦åˆ™ä¸ºæ ¼å­èƒŒæ™¯ã€‚
             * noCode ä¸æ˜¾ç¤ºä»£ç ç¼–è¾‘å™¨ã€‚
             * noPreview ä¸æ˜¾ç¤ºä»£ç é¢„è§ˆæ•ˆæœã€‚
             * noScroll é¢„è§ˆåŒºåŸŸä¸æ˜¾ç¤ºæ»šåŠ¨æ¡ã€‚
             * codePen æ˜¾ç¤º Codepen æŒ‰é’®ï¼Œè¦ç‰¹åˆ«æ³¨æ„ åŒ…å¯¼å…¥çš„é—®é¢˜ï¼Œå®ä¾‹ä¸­çš„ import ä¸»è¦ç”¨äº Codepen ä½¿ç”¨ã€‚
             */
            code: ({ inline, node, ...props }) => {
              const {
                noPreview,
                noScroll,
                bgWhite,
                noCode,
                codePen,
                codeSandbox,
              } = props;
              if (inline) {
                return <code {...props} />;
              }
              const config = {
                noPreview,
                noScroll,
                bgWhite,
                noCode,
                codePen,
                codeSandbox,
              };
              if (
                Object.keys(config).filter((name) => config[name] !== undefined)
                  .length === 0
              ) {
                return <code {...props} />;
              }
              return (
                <Code
                  code={getCodeStr(node.children)}
                  dependencies={this.props.dependencies}
                  {...{
                    noPreview,
                    noScroll,
                    bgWhite,
                    noCode,
                    codePen,
                    codeSandbox,
                  }}
                />
              );
            },
          }}
        />
        {editePath && (
          <div className={styles.footer}>
            <a href={editePath}>ç¼–è¾‘å½“å‰é¡µé¢å†…å®¹</a>
          </div>
        )}
        <Footer placement="left" />
      </Fragment>
    );
  }
}
